<template>
  <div id="avatars-component">
    <div
      v-for="(item, index) in data"
      :key="item.id"
      class="avatar"
      :style="getStyles(item, index)"
    ></div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { formatImageUrl } from "@/utils";

const getStyles = (item: any, index: number) => ({
  backgroundImage: `url("${formatImageUrl(item.profile_photo)}")`,
  left: `${index * 20}px`
});

const Component = defineComponent({
  name: "Avatars",
  props: {
    data: {
      type: Array,
      default: () => []
    }
  },
  setup() {
    return {
      getStyles
    };
  }
});
export default Component;
</script>

<style lang="stylus" scoped>
#avatars-component
  position relative
  .avatar
    position absolute
    top 0
    box-sizing border-box
    width 30px
    height 30px
    border-radius 50%
    border 1px solid white
    background-size contain
    background-repeat no-repeat
    background-position center
</style>
