<template>
  <div id="project-card-component" @click.prevent="onClick">
    <div class="top-container">
      <div class="left-container">
        <time class="date">{{
          dayjs(data.create_at).format("MM DD, YYYY")
        }}</time>
        <div class="title-wrapper">
          <h2 class="title">{{ data.name }}</h2>
          <p>{{ data.newest_stage_name }}</p>
        </div>
      </div>
      <div class="right-container">
        <img
          :src="formatImageUrl(data.start_fabric.image_url) + '@100-100'"
          :alt="data.name"
        />
      </div>
    </div>
    <div class="progress-container">
      <el-progress
        :stroke-width="15"
        :percentage="data.progress * 100"
        :text-inside="true"
        status="success"
      >
        <span></span>
      </el-progress>
    </div>
    <div class="bottom-container">
      <Avatars :data="data.joined_member"></Avatars>
      <div class="progress-text">Progress {{ data.progress * 100 }}%</div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import dayjs from "dayjs";
import Avatars from "@/components/Avatars.vue";
import { Project } from "@/typings/project";
import { formatImageUrl } from "@/utils";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "ProjectCard",
  props: {
    data: {
      type: Object as PropType<Project>,
      default: () => ({})
    }
  },
  components: {
    Avatars
  },
  setup(props) {
    const router = useRouter();
    const { data } = toRefs(props);

    const onClick = async () => {
      await router.push({
        name: "ProjectDetail",
        params: { id: data.value.id }
      });
    };

    return {
      onClick,

      dayjs,
      formatImageUrl
    };
  }
});

export default Component;
</script>

<style lang="stylus" scoped>
#project-card-component
  cursor pointer
  background white
  border-radius 15px
  padding 20px 30px
  .top-container
    flex(space-between)

    .left-container
      .date
        display inline-block
        font-size $font-size-large
        margin-bottom 30px
      .title-wrapper
        .title
          font-size $font-size-lg4x
          padding-bottom 6px
        p
          font-size $font-size-large

    .right-container
      img
        width 100px
        height 100px

  .progress-container
    padding 30px 0 15px

  .bottom-container
    height 40px
    flex(space-between)
</style>
