
import { defineComponent, ref, reactive } from "vue";
import CollectionCardSelectable from "@/components/CollectionCardSelectable.vue";
import FabricCardSelectable from "@/components/FabricCardSelectable.vue";
import { OptionItem } from "@/typings/asideFilter";
import { useRouter } from "vue-router";
import {
  FabricCardSelectableEmit,
  FabricResult,
  FabricsParams
} from "@/typings/fabric";
import { ProjectCreateInputs } from "@/typings/project";
import { getAvaliableOptions, postCreateProject } from "@/apis/project";
import { getCollection, getCollections } from "@/apis/collection";
import {
  CollectionCardResponse,
  CollectionFabrics,
  CollectionsParams
} from "@/typings/collection";
import { getFabrics } from "@/apis/fabric";

const getDialogWidth = (step: number) => {
  return ["", "30", "80%", "80%", "80%", "40%"][step];
};

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    CollectionCardSelectable,
    FabricCardSelectable
  },
  setup() {
    const router = useRouter();

    const visible = ref(false);
    const step = ref(1);
    const page = ref(1);
    const pageCount = ref(0);

    const collectionOptions = ref<CollectionCardResponse[]>([]);
    const fabricOptions = ref<FabricResult[]>([]);
    const brandOptions = ref<OptionItem[]>([]);
    const garmentFactoryOptions = ref<OptionItem[]>([]);
    const form = reactive<ProjectCreateInputs>({
      name: "",
      expected_end_at: null,
      price: 0,
      start_fabric: 0,
      brand: null,
      garment_factory: null,
      collection: null
    });

    const init = async () => {
      const brandResponse = await getAvaliableOptions("brand");
      const garmentFactoryResponse = await getAvaliableOptions(
        "garment_factory"
      );
      brandOptions.value = brandResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
      garmentFactoryOptions.value = garmentFactoryResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
    };

    const open = async () => {
      step.value = 1;
      visible.value = true;
      await init();
    };

    const onGoCollections = async () => {
      step.value = 2;
      const params: CollectionsParams = {};
      const { data } = await getCollections(params);
      collectionOptions.value = data;
    };

    const onGoCollectionFabrics = async (id: number) => {
      step.value = 3;
      const { data } = await getCollection(id.toString());
      fabricOptions.value = data.collection_fabrics.map(
        (it: CollectionFabrics) => it.fabric
      );
    };

    const onGoFabrics = async () => {
      step.value = 4;
      const params: FabricsParams = {};
      const { data } = await getFabrics(params);
      fabricOptions.value = data.results;
      page.value = data.total_pages;
      pageCount.value = data.count;
    };

    const onCollectionSelect = (data: FabricCardSelectableEmit) => {
      if (data.selected) return;
      form.collection = data.id;
    };

    const onFabricSelect = (data: FabricCardSelectableEmit) => {
      if (data.selected) return;
      form.start_fabric = data.id;
    };

    const onPaginationChange = async (_page: number) => {
      const params: FabricsParams = { page: _page };
      const { data } = await getFabrics(params);
      fabricOptions.value = data.results;
      page.value = data.total_pages;
      pageCount.value = data.count;
    };

    const onConfirm = async () => {
      const { data } = await postCreateProject(form);
      visible.value = false;
      await router.push({ name: "ProjectDetail", params: { id: data.id } });
    };

    // Initial
    const onDialogClosed = () => {
      step.value = 1;
      page.value = 1;
      pageCount.value = 0;
      collectionOptions.value = [];
      fabricOptions.value = [];
      brandOptions.value = [];
      garmentFactoryOptions.value = [];
      form.name = "";
      form.expected_end_at = null;
      form.price = 0;
      form.start_fabric = 0;
      form.brand = null;
      form.garment_factory = null;
      form.collection = null;
    };

    return {
      visible,
      step,
      pageCount,
      form,
      collectionOptions,
      fabricOptions,
      brandOptions,
      garmentFactoryOptions,

      open,
      onGoCollections,
      onGoCollectionFabrics,
      onGoFabrics,
      onCollectionSelect,
      onFabricSelect,
      onPaginationChange,
      onConfirm,
      onDialogClosed,

      getDialogWidth
    };
  }
});

export default Component;
