
import { defineComponent } from "vue";
import { formatImageUrl } from "@/utils";

const getStyles = (item: any, index: number) => ({
  backgroundImage: `url("${formatImageUrl(item.profile_photo)}")`,
  left: `${index * 20}px`
});

const Component = defineComponent({
  name: "Avatars",
  props: {
    data: {
      type: Array,
      default: () => []
    }
  },
  setup() {
    return {
      getStyles
    };
  }
});
export default Component;
